* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* ==========HOME TABLET========== */


@media (min-width: 1280px) {
    h1 {
        font-size: 50px;
        line-height: 60.95px;
    }

    .header-section {
        height: 446px;
    }

    .main-container {
        max-width: 946px;
        gap: 50px;
    }

    .forms {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 30px;
    }

    .card-img {
        width: 180px;
        height: 227px;
        border-radius: 50px 0px 50px 0px;
        object-fit: cover;
    }

    .card {
        width: 196px;
    }

    .second-border {
        width: 153px;
        height: 143px;
        top: -22px;
        right: 0;
    }

    .footer-container {
        width: 521px;
    }
}

@media (max-width: 1279px) {
    h1 {
        font-size: 30px;
        line-height: 36.57px;
    }

    .header-section {
        height: 364px;
    }

    .main-container {
        max-width: 768px;
        gap: 50px;
    }

    .forms {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 30px;
    }

    .card-img {
        width: 180px;
        height: 227px;
        border-radius: 50px 0px 50px 0px;
        object-fit: cover;
    }

    .card {
        width: 196px;
    }

    .second-border {
        width: 153px;
        height: 143px;
        top: -22px;
        right: 0;
    }

    .footer-container {
        width: 521px;
    }
}

@media (max-width: 767px) {
    .forms {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    .card-img {
        width: 128px;
        height: 161px;
        border-radius: 50px 0px 50px 0px;
        object-fit: cover;
    }

    .card {
        width: 138px;
    }

    .second-border {
        width: 109px;
        height: 101px;
        top: -15px;
        right: -4px;
    }

    .main-container {
        max-width: 296px;
        gap: 20px;
    }

    .footer-container {
        width: 252px;
    }
}


/* =======HOME DESKTOP========= */

.container {
    height: 100vh;
    margin: auto;
    position: relative;
}

.header-container {
    /* width: 1280px; */
    margin: auto;
}

.header-section {
    background-image: url(./images/pic.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    text-align: center;
}

.forms {
    padding-top: 50px;
}

.form {
    background: #FFFFFF33;

}


.input,
.select {
    width: 270px;
    height: 40px;
    gap: 0px;
    border-radius: 20px;
    opacity: 200%;
    box-shadow: none;
    border-width: 0px;
    padding-left: 20px;
    color: #939393;
}

.inputForm {
    position: relative;
}

.magnifying {
    position: absolute;
    top: 15px;
    right: 19px;
}

.select {
    padding-right: 20px;
    position: relative;
    appearance: none;
}

::placeholder {
    color: #939393;
}

option {
    color: white;
    padding: 10px;
    background-color: #939393;
}

.logo {
    position: absolute;
    top: 0;
    left: 0;
}

h1 {
    color: #DC56C5;
    text-align: center;
    font-family: Montserrat, sans-serif;
    font-weight: 900;
}

.dropdown-form {
    position: relative;
}

.dropdown-arrow {
    position: absolute;
    top: 17px;
    right: 16px;
}

.card-img {
    border-radius: 50px 0px 50px 0px;
    object-fit: cover;
}

/* MAIN SECTION */

.main-container {
    margin: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: 100%;
    justify-content: center;
    padding-top: 50px;
}

.main-section-container {
    margin: auto;
}

.main-section {
    background-image: url(./images/bg-body.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.failed {
    background-image: url(./images/bg-body.png);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
}



.event-name {
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 19.5px;
    color: #DC56C5;
    text-align: center;
}

.card {
    display: flex;
    flex-direction: column;
    gap: 8px;
    cursor: pointer;
    position: relative;
}

.date {
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.5px;
    text-align: center;
    color: #FFFFFF;
}

.location {
    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 17.07px;
    text-align: center;
    color: #FFFFFF;
}

.location-icon {
    width: 7px;
    height: 10px;
}

.sample-icon {
    display: none;
}

.event-info {
    display: flex;
    flex-direction: column;
    gap: 8px;
    justify-content: flex-end;
}

/* SECOND BORDER */

.second-border {
    position: absolute;
    border-radius: 50px 0px 50px 0px;
    border: 1px solid #DC56C54D;

}




/* FOOTER */

footer {
    padding-top: 50px;
    padding-bottom: 30px;
    margin: auto;
}

footer p {
    color: #939393;
    font-family: Montserra, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.5px;
    text-align: center;
}

footer span {
    color: red;
    font-size: 20px;
}



/* PAGINATION */

.pagination {
    padding-top: 50px;
    display: flex;
    justify-content: center;
    margin: auto;
}

.pagination ul {
    display: flex;
    gap: 15px;
}

.pagination li {
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.5px;
    text-align: center;
    color: #FFFFFF;
    list-style: none;
    padding-top: 5px;
    cursor: pointer;
}

.pagination .active {
    color: #DC56C5;
    width: 30px;
    height: 30px;
    border-radius: 10px 0px 10px 0px;
    border: 1px solid #DC56C5;
}

.current-page {
    display: none;
}

/* MODAL STYLES */
.modal-container {
    font-family: Montserrat;
}

.backdrop {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.event-modal {
    background-color: #FFF2FD;
    width: 1000px;
    height: 100vh;
    position: fixed;
    top: 5px;
    align-items: center;
    justify-content: center;
    z-index: 5;
    border-top-left-radius: 300px;
    border-bottom-right-radius: 300px;
}

.is-hidden {
    pointer-events: none;
    opacity: 0;
}

.modal-close-button {
    background-color: transparent;
    border-color: transparent;
    position: absolute;
    right: 20px;
    top: 20px;
    fill: #4C00FE;
}

.circle-event-img {
    height: 128px;
    border-radius: 100%;
    margin-top: 20px;
    margin-left: 450px;
}

.event {
    display: flex;
    flex-direction: row;
    justify-content: start;
    margin-top: 10px;
    align-items: start;
    column-gap: 40px
}

.event-img {
    width: 400px;
    height: 275px;
    margin-left: 130px;
}

.info-heading {
    font-size: 20px;
    font-weight: 600;
    line-height: 29.26px;
    color: #DC56C5;
}

.info-description {
    font-weight: 400;
    font-size: 14px;
    line-height: 21.94px;
    color: #0E0E0E;
    margin-right: 50px;
}

.artist-info,
.ticket-info {
    margin-top: 10px;
    margin-left: 150px;
}

.ticket-prices {
    display: flex;
}

.barcode-image {
    margin-right: 30px;
}

.buy-button {
    height: 40px;
    width: 145px;
    font-weight: 500;
    font-family: inherit;
    border-radius: 5px;
    color: #FFFFFF;
    background-color: #4C00FE;
    border: none;
    margin-top: 10px;
    margin-right: 120px;
    font-size: 14px;
}

.more-button {
    margin-top: 30px;
    margin-left: 240px;
    height: 40px;
    width: 255px;
    border-radius: 5px;
    font-family: inherit;
    font-weight: 500;
    font-size: 14px;
    color: #4C00FE;
    background-color: #FFF2FD;
    border: 1px solid #4C00FE;
    border: 1px solid #4C00FE;
}
